.App {
  background-color: #f0f0f0;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}
.App div {
  background-color: #f5f5f5;
  margin: 1em auto;
  padding: 1em;
  width: 80%;
}
a {
  color: inherit;
  font-weight: bold;
  text-decoration: none;
}
code {
  background-color: #f0f0f0;
  display: block;
  margin: 1em;
  padding: 0.5em;
}
div h2 {
  margin-top: 0;
}
footer {
  background-color: #ff4500;
  text-align: center;
}
footer * {
  color: #f5f5f5;
}
footer a {
  display: block;
  margin: 0.5em auto;
}
main#docs img {
  height: auto;
  max-width: 400px;
  width: 100%;
}
h2 {
  margin: 0.5em 0;
}
h3 {
  margin: 0.25em 0;
}
header {
  background-color: #f5f5f5;
  display: grid;
  grid-template-columns: auto auto auto 1fr auto;
  padding: 0.5em;
}
header * {
  margin: 0;
  vertical-align: middle;
}
header a {
  display: grid;
  grid-template-columns: auto auto;
}
header h1 {
  margin-right: 0.25em;
}
header img {
  height: 3em;
  padding-right: 0.5em;
  width: 3em;
}
header span {
  grid-column: 5;
}
header span span {
  cursor: pointer;
}
li {
  padding-bottom: 0.5rem;
}
